import { NgModule } from '@angular/core';

import { RouterModule, Routes, PreloadAllModules } from '@angular/router';

import { AuthGuard } from './guards/auth.guard';
import { DataResolver } from './app.resolver';

export const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'dashboard', loadChildren: 'app/dashboard/dashboard.module#DashboardModule', canLoad: [ AuthGuard ] },
  { path: 'user', loadChildren: 'app/user/user.module#UserModule', canLoad: [ AuthGuard ] },
  { path: 'question', loadChildren: 'app/question/question.module#QuestionModule', canLoad: [ AuthGuard ] },
  { path: 'news', loadChildren: 'app/news/news.module#NewsModule', canLoad: [ AuthGuard ] },
  { path: 'advertise', loadChildren: 'app/advertise/advertise.module#AdvertiseModule', canLoad: [ AuthGuard ] },
  { path: 'clan', loadChildren: 'app/clan/clan.module#ClanModule', canLoad: [ AuthGuard ] },
  { path: 'clanstats', loadChildren: 'app/clan-stat/clan-stat.module#ClanStatModule', canLoad: [ AuthGuard ] },
  { path: 'statistic', loadChildren: 'app/statistic/statistic.module#StatisticModule', canLoad: [ AuthGuard ] },
  { path: 'giftcodes', loadChildren: 'app/giftcodes/giftcodes.module#GiftcodesModule', canLoad: [ AuthGuard ] },
  { path: 'cminer', loadChildren: 'app/cminer/cminer.module#CminerModule', canLoad: [ AuthGuard ] },
  { path: 'zarinpal', loadChildren: 'app/zarinpal/zarinpal.module#ZarinpalModule', canLoad: [ AuthGuard ] },
];

@NgModule({
	imports: [ RouterModule.forRoot(routes, {useHash: true, preloadingStrategy: PreloadAllModules}) ],
	exports: [ RouterModule ],
})
export class AppRoutingModule {}



